@font-face {
  font-family: robotolight;
  src: url('../public/fonts/roboto/woff2/roboto-light-webfont.woff2') format('woff2'),
  url('../public/fonts/roboto/woff/roboto-light-webfont.woff') format('woff'),
  url('../public/fonts/roboto/ttf/roboto-light-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
};


@font-face {
  font-family: "interlight";
  src: url("../public/fonts/inter/Inter-Light.woff2") format("woff2"),
    url("../public/fonts/inter/Inter-Light.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}


@font-face {
  font-family: "intermedium";
  src: url("../public/fonts/inter/Inter-Medium.woff2") format("woff2"),
    url("../public/fonts/inter/Inter-Medium.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "interregular";
  src: url("../public/fonts/inter/Inter-Regular.woff2") format("woff2"),
    url("../public/fonts/inter/Inter-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*{
  padding: 0;
  margin: 0;
  border: 0;
  user-select: none;
}
*,*:before,*:after{
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  touch-action: pan-x pan-y;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}


h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}